export default {
  computed: {
    linkStyle() {
      const style = {
        left: "76px",
        width: "48px"
      };
      switch (this.$route.name) {
        case "introduce":
          style.width = "80px";
          style.left = "260px";
          break;
        case "news":
          style.width = "80px";
          style.left = "460px";
          break;
        case "activity":
          style.width = "80px";
          style.left = "660px";
          break;
        case "publish":
          style.width = "80px";
          style.left = "860px";
          break;
        case "connect":
          style.width = "80px";
          style.left = "1060px";
          break;
      }
      return style;
    }
  },
  mounted() {
    console.log(this.$route);
  }
};
